// ** Reactstrap Imports
import { Card, CardHeader, Progress } from 'reactstrap'

// ** Third Party Components
import { ChevronDown } from 'react-feather'
import DataTable from 'react-data-table-component'

// ** Custom Components
import Avatar from '@components/avatar'
import { timeSheetList } from '../store/index'
// ** Styles
import '@styles/react/libs/tables/react-dataTable-component.scss'
import { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { useParams } from 'react-router-dom'
import moment from 'moment'

const dateFormat = (value) => {

  return moment.unix(value).format("MMM DD, YYYY")

}

export const columns = [
  {
    name: 'Start Time',
    cell: row => dateFormat(row.starttime)
  },
  {
    name: 'End Time',
    cell: row => (row.endtime !== null ? dateFormat(row.endtime) : '-')
  },
  {
    name: 'Total Hours',
    cell: row => row.totalhours || '0'
  },
  {
    name: 'Amount',
    selector: row => row.amount || '0'
  }
]

const UserProjectsList = (data) => {

  const dispatch = useDispatch()
  const { id } = useParams()
  const [dataArr, setData] = useState([])

  const getTimeSheetList = async () => {
    const res = await dispatch(timeSheetList({ taskId: id }))    
    setData(res.payload)
  }
  
  useEffect(async () => {
    if (data.tabId === 'timesheets') {
      getTimeSheetList()
    }
  }, [data])

  return (
    <Card>
      <CardHeader tag='h4'>Time Sheet List</CardHeader>
      <div className='react-dataTable user-view-account-projects'>
        <DataTable
          noHeader
          responsive
          columns={columns}
          data={dataArr}
          className='react-dataTable'
          sortIcon={<ChevronDown size={10} />}
        />
      </div>
    </Card>
  )
}

export default UserProjectsList
