// ** React Imports
import { Fragment, useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
// ** Reactstrap Imports
import { Row, Col, Card, CardBody, Badge, Table, Nav, NavItem, NavLink, TabContent, TabPane, Button } from 'reactstrap'
import classnames from 'classnames'
// ** Third Party Components
import Conversation from './Conversation'
import CheckList from './CheckList'
import TimeSheet from './TimeSheet'
import Templates from './Templates'
import Swal from 'sweetalert2'
import { Bell, Briefcase, Calendar, Clock, Edit, FileText, MessageCircle, Paperclip, User, X } from 'react-feather'
import withReactContent from 'sweetalert2-react-content'

// ** Custom Components
import axios from '@src/configs/axios/axiosConfig'
import { startTimer, endTimer, updateStatus } from '../store/index'
// ** Styles
import { useDispatch } from 'react-redux'
import '@styles/react/libs/react-select/_react-select.scss'
import moment from 'moment'
import Select from 'react-select'
import { orgUserId, activeOrganizationid } from '@src/helper/sassHelper'
const userId = orgUserId()
const activeOrgId = activeOrganizationid()

const statusOptions = [{ id: 1, name: 'To Do' }, { id: 2, name: 'Inprogress' }, { id: 3, name: 'Completed' }, { id: 4, name: 'On Hold' }, { id: 5, name: 'Cancelled' }, { id: 6, name: 'Sent to Review' }, { id: 7, name: 'Request to changes' }]

const priorityColors = [
  '',
  'light-success',
  'light-warning',
  'light-danger'
]

const priorityArr = [
  '',
  'Low',
  'Medium',
  'High'
]

const MySwal = withReactContent(Swal)

const UserInfoCard = ({ selectedTask }) => {

  const { id } = useParams()
  // ** State
  const dispatch = useDispatch()
  const [active, setActive] = useState('tasknotes')
  const [taskStatus, setTaskStatus] = useState(1)
  const [timerFlag, setTimerFlag] = useState(false)
  const [hourlyRate, setHourlyRate] = useState(0)

  const toggleTab = tab => {
    if (active !== tab) {
      setActive(tab)
    }
  }

  const dateFormat = (value) => {
    return moment.unix(value).format("MMM DD, YYYY")
  }

  const getEmployeeDet = () => {
    axios.post('/organizationusers/getjobinfo', { id: userId })
      .then((res) => {
        setHourlyRate(res.data.organizationusers.hourlyrate || 0)
      }).catch((err) => {
        console.log(err)
      })
  }

  const startTaskTimer = async () => {
    const data = {
      createdBy: userId,
      taskId: id,
      userId,
      hourlyRate,
      organizationId: activeOrgId,
      startTime: moment().unix()
    }
    const res = await dispatch(startTimer(data))
    localStorage.setItem('currentTask', JSON.stringify(res.payload))
    setTimerFlag(true)
  }

  const stopTaskTimer = async () => {
    const temp = JSON.parse(localStorage.getItem('currentTask'))
    if (temp !== undefined || temp !== null) {
      const end = moment()
      const start = moment.unix(temp.starttime).toDate()
      const end_timestamp = moment().unix()
      const duration = moment.duration(end.diff(start))
      const hours = duration.asHours()
      const amount = hourlyRate * hours
      const data = {
        updatedBy: userId,
        id: temp.id,
        totalhours: hours,
        amount,
        hourlyRate,
        userId,
        endTime: end_timestamp
      }
      await dispatch(endTimer(data))
      localStorage.removeItem('currentTask')
      setTimerFlag(false)
    }
  }

  const updateTaskStatus = async (status) => {
    const data = {
      updatedBy: userId,
      taskId: id,
      status
    }
    await dispatch(updateStatus(data))
  }

  useEffect(() => {
    if (selectedTask !== null) {
      setTaskStatus(selectedTask.taskstatus)

      getEmployeeDet()

      const temp = localStorage.getItem('currentTask')
      if (temp !== undefined && temp !== null) {
        setTimerFlag(true)
      }
    }

  }, [selectedTask])


  return (
    <Fragment>
      <Row className='my-1'>
        <Col className='d-flex justify-content-between'>
          <div>
            <span>By {selectedTask.createdBy}</span>&nbsp;|&nbsp; <span>
              <Briefcase size={14} /> {selectedTask.servicename}
            </span> &nbsp;|&nbsp;
            <span> <MessageCircle size={14} /> 4 </span>
            &nbsp;|&nbsp;
            <span> <Paperclip size={14} /> 4 </span>
            &nbsp;|&nbsp;
            <span> <Clock size={14} /> </span>
          </div>
          <div>
            <Edit size={16} className='ms-1 cursor-pointer' tag={Link} to={`/task/edit/${id}`} />
            <X size={16} className='ms-1 cursor-pointer' tag={Link} to={`/task/list`} />
          </div>
        </Col>

      </Row>
      <Card>
        <CardBody>

          <Table responsive bordered>
            <thead>
              <tr >
                <th colSpan='3'>Task Information</th>
                <th>
                  <Row className='justify-content-end'>
                    <div>
                      {(!timerFlag || !selectedTask.timesheetFlag) &&
                        <Button size='sm' disabled={!selectedTask.timesheetFlag} outline color='primary' onClick={() => { startTaskTimer() }}>
                          <Clock size={14} /> Start Timer</Button>
                      }
                      {(timerFlag && !selectedTask.timesheetFlag) &&
                        <Button size='sm' outline color='danger' onClick={() => { stopTaskTimer() }}>
                          <Clock size={14} /> Stop Timer</Button>
                      }
                    </div>
                  </Row>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>Reviewer</td>
                <td >
                  {selectedTask.userlists.map((user, key) => {

                    return (user.type === "2" && <Badge color='light-primary' pill href='#' key={key}>
                      <User size={14} />
                      <span className='align-middle ms-50'>{user.name}</span>
                    </Badge>
                    )
                  })
                  }
                </td>
                <td >
                  Assignee
                </td>
                <td>
                  {selectedTask.userlists.map((user, key) => {

                    return (user.type === "1" && <Badge color='light-primary' pill href='#' key={key}>
                      <User size={14} />
                      <span className='align-middle ms-50'>{user.name}</span>
                    </Badge>
                    )
                  })
                  }
                </td>
              </tr>
              <tr>
                <td>Status</td>
                <td >
                  <Select
                    className={classnames('react-select')}
                    classNamePrefix='select'
                    options={statusOptions}
                    getOptionLabel={(option) => option.name}
                    onChange={(val) => { updateTaskStatus(val.id); return val.id }}
                    value={statusOptions.find(c => { return c.id === taskStatus })}
                    getOptionValue={(option) => option.id}
                  />

                </td>
                <td >
                  Priority
                </td>
                <td> <Badge className='text-capitalize' color={priorityColors[selectedTask.priority]}>
                  {priorityArr[selectedTask.priority]}
                </Badge>.</td>
              </tr>
              <tr>
                <td>Start Date</td>
                <td >
                  <div className='d-flex justify-content-between' >
                    {dateFormat(selectedTask.startdate)}
                    <Calendar size={16} />
                  </div>
                </td>
                <td >
                  End Date
                </td>
                <td>
                  <div className='d-flex justify-content-between' >
                    {dateFormat(selectedTask.enddate)}
                    <Calendar size={16} />
                  </div>
                </td>
              </tr>
              <tr>
                <td>Tag</td>
                <td >
                  <Badge color='primary' className='text-capitalize'>
                    Professional Tax
                  </Badge>
                </td>
                <td >
                  Complete Percentage
                </td>
                <td>
                  0
                </td>
              </tr>
            </tbody>
          </Table>

          <Row className='mt-1 ps-1'>
            <Fragment>
              <Nav pills className='mb-2'>
                <NavItem>
                  <NavLink active={active === 'tasknotes'} onClick={() => toggleTab('tasknotes')}>
                    <MessageCircle className='font-medium-3 me-50' />
                    <span className='fw-bold'>Comments</span>
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink active={active === 'taskservicetemplate'} onClick={() => toggleTab('taskservicetemplate')}>
                    <FileText className='font-medium-3 me-50' />
                    <span className='fw-bold'>Templates</span>
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink active={active === 'checklist'} onClick={() => toggleTab('checklist')}>
                    <Bell className='font-medium-3 me-50' />
                    <span className='fw-bold'>Checklist Trail</span>
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink active={active === 'timesheets'} onClick={() => toggleTab('timesheets')}>
                    <Clock className='font-medium-3 me-50' />
                    <span className='fw-bold'>Timesheet</span>
                  </NavLink>
                </NavItem>
              </Nav>
              <TabContent activeTab={active}>

                <TabPane tabId='tasknotes'>
                  <Conversation tabId={active} />
                </TabPane>
                <TabPane tabId='taskservicetemplate'>
                  <Templates tabId={active} selectedTask={selectedTask} />
                </TabPane>
                <TabPane tabId='checklist'>
                  <CheckList tabId={active} />
                </TabPane>
                <TabPane tabId='timesheets'>
                  <TimeSheet tabId={active} />
                </TabPane>
              </TabContent>
            </Fragment>
          </Row>
        </CardBody>
      </Card>
    </Fragment>
  )
}

export default UserInfoCard
